import { OnePlayer } from '../OnePlayer'
import { IPlayer } from '../IPlayer'
import { StreamInfo } from '../Types'
import { DebugPanel } from './DebugPanel'


export class DebugController {

    // #region MEMBERS
    // --------------------------------------------------

    private player: IPlayer;

    private debugPanel: DebugPanel;

    private debug: boolean;
    private verbose: boolean;

    private onKeyPressedListener;

    // #endregion MEMBERS

    // #region CONSTRUCTOR
    // --------------------------------------------------

    /**
     * Constructor.
     */
    constructor (player: IPlayer, video: HTMLElement) {
        this.player = player;
        this.debug = false;
        this.verbose = false;
        this.onKeyPressedListener = this.onKeyPressed.bind(this);
        window.addEventListener('keydown', this.onKeyPressedListener);
        this.debugPanel = new DebugPanel(player, video);
    }

    public reset() {
        this.debugPanel.reset();
    }

    public start(stream: StreamInfo) {
        this.debugPanel.start(stream);
    }

    public stop() {
        this.debugPanel.stop();
    }
    
    public showDebugPanel (show: boolean, debugRenderingDiv?: HTMLDivElement) {
        this.debugPanel.show(show, debugRenderingDiv);
    }

    // #endregion CONSTRUCTOR    
    // --------------------------------------------------

    // #region PRIVATE METHODS
    // --------------------------------------------------

    private onKeyPressed (e: KeyboardEvent) {

        // Handle ctrl+alt+maj+d to show/hide debug panel
        if ((e.altKey === true) && (e.ctrlKey === true) && (e.shiftKey === true) && (e.keyCode === 68)) {
            this.debug = !this.debug;
            this.debugPanel.show(this.debug);
        }

        // Handle ctrl+alt+maj+v to enable/disable logs
        if ((e.altKey === true) && (e.ctrlKey === true) && (e.shiftKey === true) && (e.keyCode === 86)) {
            this.verbose = !this.verbose;
            console.log('oneplayer.js debug ' + (this.verbose ? 'ON': 'OFF'));
            let playerVersion = this.player.getVersion();
            console.log('oneplayer.js v' + OnePlayer.getVersion() + (playerVersion.length > 0) ? (' / ' + playerVersion) : '');
            this.player.enableLogs(this.verbose);
        }
    }

    // #endregion PRIVATE METHODS
    // --------------------------------------------------
}