

export class PlaybackSpeedController {

    // #region MEMBERS
    // --------------------------------------------------

    // <video> / HTMLMediaElement
    private videoElement: HTMLMediaElement;    

    // Playback speed
    private speed: number = 0;

    // Trick mode speed
    private trickModeSpeed: number = 0;

    // Original volume level
    private volume: number;

    // The time at which seek has been done
    private seekTime: number;

    // The timeout for seeking
    private seekTimeout;

    private onSeekedListener;

    private playPromise: Promise<any>;

    // #endregion MEMBERS
    // --------------------------------------------------

    // #region CONSTRUCTOR
    // --------------------------------------------------

    /**
     * Constructor.
     */
    constructor () {
    }

    // #endregion CONSTRUCTOR

    // #region PUBLIC METHODS
    // --------------------------------------------------

    public init (videoElement: HTMLMediaElement) {
        this.videoElement = videoElement;
        this.volume = null;
        this.playPromise = null;
        this.onSeekedListener = this.onSeeked.bind(this);
    }

    public reset () {
        this.stopTrickMode();

        // Restore volume level
        if (this.volume) {
            this.videoElement.volume = this.volume;
            this.volume = null;
        }
    }

    public setSpeed (speed: number) {
        // console.log('[TRICK_PLAY] setSpeed: ' + speed);

        // Ignore value 0, method pause() has to be used instead
        // Ignore negative values between -1 and 0 (not supported)
        if (speed <= 0 &&  speed > -1) {
            throw new Error('The provided playback speed (' + speed + ') is not in the supported range');
        }

        // Check if duration is available (stream initialized)
        if (isNaN(this.videoElement.duration)) {
            throw new Error('Playback speed can not be changed until video is not initialized');
        }

        this.speed = speed;

        // Apply new speed now if not already in trick mode and if playback is not being (re)started
        // => if already in trick mode, then apply new speed once current seek is done (see onSeeked)
        // => if playback is being (re)started, then apply new speed once play is done (see onPlay)
        if (this.trickModeSpeed === 0 && this.speed !== 1 && this.playPromise === null) {
            this.applySpeed();
        }
    }

    public getSpeed () : number {
        return this.trickModeSpeed === 0 ? this.videoElement.playbackRate : this.trickModeSpeed;
    }

    // #endregion PUBLIC METHODS

    // #region PRIVATE METHODS
    // --------------------------------------------------

    private applySpeed () {
        // From normal playback to slow motion or fast forward/rewind, then set mute on
        if (this.videoElement.playbackRate === 1 && this.trickModeSpeed === 0 && this.speed !== 1) {
            this.volume = this.videoElement.volume;
            // console.log('[TRICK_PLAY] mute on (' + this.volume + ')');
            this.videoElement.volume = 0;
        }

        // If normal playback or slow motion then stop trick mode
        if (Math.abs(this.speed) <= 1 && this.trickModeSpeed !== 0) {
            // console.log('[TRICK_PLAY] stop trick mode');
            this.stopTrickMode();
        }

        // For negative values and values > 1 then start/run trick mode at given speed
        if (this.speed <= -1 || this.speed > 1) {
            if (this.trickModeSpeed === 0) {
                // Switch to lowest quality
                // this.limitToLowestBitrate(true);

                // Start trick mode
                // console.log('[TRICK_PLAY] start trick mode');
                this.startTrickMode(this.speed);
            } else if (this.trickModeSpeed !== this.speed) {
                // Or modify trick mode speed
                // console.log('[TRICK_PLAY] update speed: ' + this.speed);
                this.trickModeSpeed = this.speed;
            }
        }

        // For speed between 0 and 1 (slow motion or normal playback) then simply modify the playbackRate of the HTML video element
        if (this.speed > 0 && this.speed <= 1) {
            this.videoElement.playbackRate = this.speed;
            // For normal playback, play if in paused mode
            if (this.videoElement.paused) {
                // console.log('[TRICK_PLAY] play');
                this.playPromise = this.videoElement.play().then(this.onPlay.bind(this));
            }
        }        
    }
 
    private startTrickMode (speed: number) {
        // console.log('[TRICK_PLAY] start trick mode');

        this.trickModeSpeed = speed;

        // Pause video and set playbackRate to 1
        this.videoElement.pause();
        this.videoElement.playbackRate = 1;

        // Add 'seeked' listener
        this.videoElement.addEventListener('seeked', this.onSeekedListener);

        // Start seeking
        this.seek();
    }

    private stopTrickMode () {
        // console.log('[TRICK_PLAY] stop trick mode');

        // Remove 'seeked' listener
        this.videoElement.removeEventListener('seeked', this.onSeekedListener);

        // Cancel seek timeout
        clearTimeout(this.seekTimeout);

        this.trickModeSpeed = 0;
    }

    private seek () {
        this.seekTime = new Date().getTime();
        let seekPos = this.videoElement.currentTime + this.trickModeSpeed;
        // console.log('[TRICK_PLAY] seek: ' + seekPos);
        this.videoElement.currentTime = Math.min(Math.max(seekPos, 0), this.videoElement.duration);
    }

    private onSeeked () {
        // console.log('[TRICK_PLAY] seeked');

        // On Safari we need to pause again since seeking restarts playback automatically
        this.videoElement.pause();

        let time = new Date().getTime();
        let elapsedTime = time - this.seekTime;
        // console.log('[' + time + '] seeked - elapsed time = ' + elapsedTime + ', wait for ' + Math.max((1000 - elapsedTime), 0) + ' ms');

        this.applySpeed();

        // Still in trick mode => seek again
        if (this.trickModeSpeed !== 0 &&
            this.videoElement.currentTime > 0 &&
            this.videoElement.currentTime < this.videoElement.duration) {
            this.seekTimeout = setTimeout(() => {
                this.seek();
            }, Math.max((1000 - elapsedTime), 0));
        }
    }

    private onPlay() {
        // console.log('[TRICK_PLAY] playing');

        this.playPromise = null;

        this.applySpeed();

        // If normal playback, restore volume level
        if (this.videoElement.playbackRate === 1 && this.volume) {
            // console.log('[TRICK_PLAY] mute off => ' + this.volume);
            this.videoElement.volume = this.volume;
            this.volume = null;
        }
    }

    // #endregion PRIVATE METHODS
}