import { IPlayer } from '../IPlayer'
import { DefaultPlayer } from '../DefaultPlayer'
import { PlayerType, StreamInfo, MediaType, TrackInfo, ProtectionData, AudioKind, TextKind } from '../Types'
import { FairPlayController} from './FairPlayController'
import { PlayerErrorCode } from '../Errors';

/**
 * Dash.js player
 */
export class HlsFpPlayer extends DefaultPlayer implements IPlayer {
    // #region MEMBERS
    // --------------------------------------------------

    // Protection data
    private protectionData: ProtectionData;

    // FairPlayController
    private fpcontroller: FairPlayController;

    // Default audio language
    private defaultAudioLanguage: string;

    // Default text language
    private defaultTextLanguage: string;

    // Default text state
    private defaultTextEnabled: boolean;

    // Selected text track id before being disabled
    private selectedTextTrackId: string;

    // Events listeners
    private loadedmetadataListener;
    private canplayListener;
    private seekedListener;
    private errorListener;

    // #endregion MEMBERS
    // --------------------------------------------------

    // #region CONSTRUCTOR
    // --------------------------------------------------

    /**
     * Constructor.
     */
    constructor () {
        super();
    }
    // #endregion CONSTRUCTOR

    // #region PUBLIC FUNCTIONS
    // --------------------------------------------------

    // #region SETUP
    // --------------------------------------------------

    public getVersion (): string {
        return '';
    }

    public getPlayerType (): PlayerType {
        return PlayerType.DASHJS;
    }
        
    public init (videoElement: HTMLElement, ttmlRenderingDiv?: HTMLDivElement) : Promise<any> {
        super.init(videoElement, ttmlRenderingDiv);
        this.defaultAudioLanguage = null;
        this.defaultTextLanguage = null;
        this.defaultTextEnabled = false;
        this.selectedTextTrackId = null;

        this.loadedmetadataListener = this.onLoadedmetadata.bind(this);
        this.canplayListener = this.onCanPlay.bind(this);
        this.seekedListener = this.onSeeked.bind(this);
        this.errorListener = this.onError.bind(this);

        this.videoElement.addEventListener('loadedmetadata', this.loadedmetadataListener);
        this.videoElement.addEventListener('canplay', this.canplayListener);
        this.videoElement.addEventListener('error', this.errorListener);

        return Promise.resolve();
    }

    public setConfig (params: object) {
    }
 
    public enableLogs (enable: boolean) {
        super.enableLogs(enable);
        if (this.fpcontroller) {
            this.fpcontroller.enableLogs(enable);
        }
    }

    public addEventListener (type: string, listener: any, scope?: object) {
        this.videoElement.addEventListener(type, listener, scope);
    }

    public removeEventListener (type: string, listener: any, scope?: object) {
        this.videoElement.removeEventListener(type, listener, scope);
    }

    public reset() {
        this.videoElement.removeEventListener('loadedmetadata', this.loadedmetadataListener);
        this.videoElement.removeEventListener('canplay', this.canplayListener);
        this.videoElement.removeEventListener('error', this.errorListener);
    }

    // #endregion SETUP
    // --------------------------------------------------

    // #region PLAYBACK CONTROL
    // --------------------------------------------------

    public play () {
        this.videoElement.play();
    }

    public pause () {
        this.videoElement.pause();
    }

    public seek (time: number) {
        this.videoElement.currentTime = time;
    }

    public stop (reset = true, stopPlugins?: boolean) {
        // if (this.stream === null) {
        //     return;
        // }

        super.stop(stopPlugins);

        if (reset || this.isLive()) {
            this.videoElement.pause();
            if (this.fpcontroller !== null) {
                this.fpcontroller.reset();
                this.fpcontroller = null;
            }
    
            this.videoElement.removeAttribute('src');
            this.videoElement.load();
        } else {
            this.videoElement.addEventListener('seeked', this.seekedListener);
            this.videoElement.currentTime = 0;
        }


    }

    // #endregion PLAYBACK CONTROL
    // --------------------------------------------------
    
    // #region DURATION / TIME
    // --------------------------------------------------

    public getDuration () : number {
        return this.videoElement.duration;
    }

    public isLive () {
        return this.getDuration() !== Number.POSITIVE_INFINITY ? false : true;
    }

    public getTime () : number {
        return this.videoElement.currentTime;  
    }

    // #endregion DURATION / TIME
    // --------------------------------------------------

    // #region AUDIO/SUBTITLES TRACKS
    // --------------------------------------------------

    public getTracksForType (type: MediaType): TrackInfo[] {
        switch (type) {
            case MediaType.Video:
                return this.getVideoTracks();
            case MediaType.Audio:
                return this.getAudioTracks();
            case MediaType.Text:
                return this.getTextTracks();
            default:
                return null;
        }
    } 

    public getSelectedTrackForType (type: MediaType): TrackInfo {
        switch (type) {
            case MediaType.Audio:
                return this.getSelectedAudioTrack();
            case MediaType.Text:
                return this.getSelectedTextTrack();
            default:
                return null;
        }
    }

    public getQualityForType (type: MediaType): number {
        // not available with HLS
        return -1; 
    }

    public getBufferLevelForType (type: MediaType): number {
        // not available with HLS
        return -1;
    }

    public selectTrackForType (type: MediaType, id: string) {
        let lang = null;
        switch (type) {
            case MediaType.Audio:
                lang = this.selectAudioTrack(id);
                break;
            case MediaType.Text:
                lang = this.selectTextTrack(id);
                this.selectedTextTrackId = id;
                break;
            default:
                break;
        }
        if (lang) {
            this.storeDefaultLanguage(type, lang);
            if (type === MediaType.Text) {
                this.storeTextState(true);
            }
        }
    } 

    public selectTrack (track: TrackInfo) {
        this.selectTrackForType(track.mediaType, track.id);
    }

    public setDefaultAudioLanguage (lang: string) {
        this.defaultAudioLanguage = lang;
    }

    public setDefaultTextLanguage (lang: string) {
        this.defaultTextLanguage = lang;
    }

    public enableText (enable: boolean) {
        if (enable) {
            this.selectTextTrack(this.selectedTextTrackId);
        } else {
            let textTracks = this.videoElement.textTracks;
            for (var i = 0; i < textTracks.length; i++) {
                textTracks[i].mode === 'disabled';
            }
        }
        this.storeTextState(enable);
    }

    public isTextEnabled () : boolean {
        let textTracks = this.videoElement.textTracks;
        for (var i = 0; i < textTracks.length; i++) {
            if (textTracks[i].mode === 'showing') {
                return true;
            }
        }
        return false;
    }

    public setDefaultTextEnabled (enable: boolean) {
        this.defaultTextEnabled = true;
    }

    // #endregion AUDIO/SUBTITLES TRACKS
    // --------------------------------------------------    

    // #endregion PUBLIC FUNCTIONS
    // --------------------------------------------------

    // #region PROTECTED FUNCTIONS
    // --------------------------------------------------

    protected loadStream (stream: StreamInfo) {
        super.loadStream(stream);
        this.selectedTextTrackId = null;

        if (stream.protData) {
            this.protectionData = stream.protData;
        }

        this.fpcontroller = new FairPlayController();
        this.fpcontroller.init(this.videoElement, this.protectionData);
        this.videoElement.src = stream.url;
    }

    protected limitToLowestBitrate(state: boolean) {
        // Using built-in video player there is no mean to control bitrate
        return;
    }

    // #endregion PROTECTED FUNCTIONS

    // #region PRIVATE FUNCTIONS
    // --------------------------------------------------

    private getVideoTracks (): TrackInfo[] {
        let tracksInfo: TrackInfo[] = [];
        let trackInfo: TrackInfo = {
            mediaType: MediaType.Video,
            id: 'video',
            selected: true,
            codec: '',
            mimeType: '',
            lang: '',
            qualities: []
        };
        tracksInfo.push(trackInfo);
        return tracksInfo;
    }

    private getAudioTracks (): TrackInfo[] {
        let tracksInfo: TrackInfo[] = [],
            audioTracks = this.videoElement.audioTracks;

        if (audioTracks.length === 0) {
            return [];
        }
    
        for (var i = 0; i < audioTracks.length; i++) {
            tracksInfo.push(this.audioTrackToMediaInfo(audioTracks[i]));
        }
        return tracksInfo;
    }

    private getTextTracks (): TrackInfo[] {
        let tracksInfo: TrackInfo[] = [],
            textTracks = this.videoElement.textTracks;

        if (textTracks.length === 0) {
            return [];
        }
    
        for (var i = 0; i < textTracks.length; i++) {
            tracksInfo.push(this.textTrackToMediaInfo(textTracks[i]));
        }
        return tracksInfo;
    }

    private selectAudioTrack(id?: string, lang?: string) {
        let audioTracks = this.videoElement.audioTracks;
        let selectedLang = null;
        for (var i = 0; i < audioTracks.length; i++) {
            if ((id && audioTracks[i].id === id) || (lang && audioTracks[i].language === lang)) {
                audioTracks[i].enabled = true;
                selectedLang = audioTracks[i].language;
            }
        }
        return selectedLang;
    }

    private selectTextTrack(id?: string, lang?: string) {
        let textTracks = this.videoElement.textTracks;
        let selectedLang = null;
        for (var i = 0; i < textTracks.length; i++) {
            if ((id && textTracks[i].label === id) || (lang && textTracks[i].language === lang)) {
                textTracks[i].mode = 'showing';
                selectedLang = textTracks[i].language;
            } else {
                textTracks[i].mode = 'disabled';
            }
        }
        if (!selectedLang && textTracks.length > 0) {
            textTracks[0].mode = 'showing';
            selectedLang = textTracks[0].language;
        }
        return selectedLang;
    }

    private getSelectedAudioTrack() {
        let audioTracks = this.videoElement.audioTracks;
        for (var i = 0; i < audioTracks.length; i++) {
            if (audioTracks[i].enabled) {
                return this.audioTrackToMediaInfo(audioTracks[i]);
            }
        }
        return null;
    }

    private getSelectedTextTrack() {
        let textTracks = this.videoElement.textTracks;
        for (var i = 0; i < textTracks.length; i++) {
            if (textTracks[i].mode === 'showing') {
                return this.textTrackToMediaInfo(textTracks[i]);
            }
        }
        return null;
    }

    private audioTrackToMediaInfo(audioTrack: AudioTrack) {
        let trackInfo: TrackInfo = {
            mediaType: MediaType.Audio,
            id: audioTrack.id,
            selected: audioTrack.enabled,
            kind: audioTrack.kind as AudioKind,
            codec: '',
            mimeType: '',
            lang: audioTrack.language,
            qualities: []
        };
        return trackInfo;
    }

    private textTrackToMediaInfo(textTrack: TextTrack) {
        let trackInfo: TrackInfo = {
            mediaType: MediaType.Text,
            id: textTrack.label,
            selected: textTrack.mode === 'showing',
            kind: textTrack.kind as TextKind,
            codec: '',
            mimeType: '',
            lang: textTrack.language,
            qualities: []
        };

        return trackInfo;
    }    

    private onLoadedmetadata (e) {
        if (this.defaultAudioLanguage) {
            this.selectAudioTrack(undefined, this.defaultAudioLanguage);
        }
        if (this.defaultTextLanguage && this.defaultTextEnabled) {
            this.selectTextTrack(undefined, this.defaultTextLanguage)
        }
    }

    private onCanPlay (e) {
        // autoplay
        this.videoElement.play();
    }

    private onSeeked (e) {
        // used by stop method
        console.log('seeked');
        this.videoElement.pause();
        this.videoElement.removeEventListener('seeked', this.seekedListener);
    }

    private onError (e) {
        switch (e.target.error.code) {
            // MEDIA_ERR_ABORTED
            case 1:
                this.sendErrorEvent(PlayerErrorCode.MEDIA_ERR_ABORTED);
                break;
            // MEDIA_ERR_NETWORK
            case 2:
                this.sendErrorEvent(PlayerErrorCode.DOWNLOAD_ERR_CONTENT);
                break;
            // MEDIA_ERR_DECODE
            case 3:
                this.sendErrorEvent(PlayerErrorCode.MEDIA_ERR_DECODE);
                break;
            // MEDIA_ERR_SRC_NOT_SUPPORTED
            case 4:
                this.sendErrorEvent(PlayerErrorCode.DOWNLOAD_ERR_MANIFEST, '', {
                    url: e.target.src,
                });
                break;
            // MEDIA_ERR_ENCRYPTED
            case 5:
                this.sendErrorEvent(PlayerErrorCode.MEDIA_ERR_ENCRYPTED);
                break;
        }
    }

    // #endregion PRIVATE FUNCTIONS

}
