import { IPlugin } from './IPlugin'
import { PluginType, StreamInfo, PlayerEvents } from '../Types'
import { IPlayer } from '../IPlayer'
import { DefaultPlayer } from '../DefaultPlayer'
import { AdsPlayer, EventTypes, ErrorCodes } from 'adsplayer.js';

/**
 * adsplayer.js plugin
 */
export class AdsPlugin implements IPlugin {
    // #region MEMBERS
    // --------------------------------------------------

    // <video> / HTMLMediaElement
    protected videoElement: HTMLMediaElement; 

    // IPlayer instance
    protected player: DefaultPlayer;

    // AdsPlayer instance
    private adsPlayer: AdsPlayer;

    // The HTML5 div to render ads
    private adsRenderingDiv;

    // Parameter to indicate adsplayer.js to handle or not main video playback
    private handleMainPlayerPlayback;

    // #endregion MEMBERS
    // --------------------------------------------------

    // #region CONSTRUCTOR
    // --------------------------------------------------

    /**
     * Constructor.
     */
    constructor () {
    }
    // #endregion CONSTRUCTOR

    // #region PUBLIC FUNCTIONS
    // --------------------------------------------------

    // #region SETUP
    // --------------------------------------------------

    public getType (): string {
        return PluginType.METRICS
    }

    public getVersion (): string {
        this.checkInitialized();
        return 'adsplayer.js_' + this.adsPlayer.getVersion();
    }
    
    public init (player: IPlayer, videoElement: HTMLMediaElement, config: object) {

        if (!config['adsRenderingDiv']) {
            throw new Error('Failed to initialize Ad-insertion plugin: div for rendering ads must be provided');
        }

        this.handleMainPlayerPlayback = true;
        if (config['handleMainPlayerPlayback']) {
            this.handleMainPlayerPlayback = config['handleMainPlayerPlayback'];
        }

        this.videoElement = videoElement;
        this.player = player as DefaultPlayer;
        this.adsRenderingDiv = config['adsRenderingDiv'];
        this.adsPlayer = new AdsPlayer();
        this.adsPlayer.init(this.videoElement, this.adsRenderingDiv, this.handleMainPlayerPlayback);
        this.adsPlayer.addEventListener(EventTypes.ERROR, this.onError.bind(this));
        this.adsPlayer.addEventListener(EventTypes.START, this.onStart.bind(this));
        this.adsPlayer.addEventListener(EventTypes.END, this.onEnd.bind(this));
        this.adsPlayer.addEventListener(EventTypes.ADD_ELEMENT, this.onAddElement.bind(this));
        this.adsPlayer.addEventListener(EventTypes.REMOVE_ELEMENT, this.onRemoveElement.bind(this));
        this.adsPlayer.addEventListener(EventTypes.PLAY, this.onPlay.bind(this));
        this.adsPlayer.addEventListener(EventTypes.PAUSE, this.onPause.bind(this));
        this.adsPlayer.addEventListener(EventTypes.CLICK, this.onClick.bind(this));
    }

    public enableLogs (enable: boolean) {
        this.adsPlayer.enableLogs(enable);
    }

    public reset() {
        this.checkInitialized();
        this.adsPlayer.destroy();
    }

    // #endregion SETUP
    // --------------------------------------------------

    // #region PLUGIN CONTROL
    // --------------------------------------------------

    public load (stream: StreamInfo): Promise<any> {
        this.checkInitialized();
        return new Promise<any>((resolve, reject) => {
            this.adsPlayer.load(stream)
            .then((res) => {
                resolve(res ? 'preroll' : '');
            })
            .catch((e) => {
                resolve();
            })    
        });
    }

    public stop () {
        this.checkInitialized();
        this.adsPlayer.reset();
        this.videoElement.style.display = 'block';
        this.adsRenderingDiv.style.display = 'none';
    }

    // #endregion PLUGIN CONTROL
    // --------------------------------------------------
    
    
    // #region PRIVATE FUNCTIONS
    // --------------------------------------------------

    private checkInitialized() {
        if (!this.adsPlayer) {
            throw 'Ad-Insertion plugin not initialized';
        } 
    }

    private onError (e) {
        let error = {
            code: e.data.code,
            message:  e.data.message,
            extMessage: '',
            data: e.data.data
        }
        this.sendEvent(PlayerEvents.AD_ERROR, error);
    }

    private onStart (e) {
        this.sendEvent(PlayerEvents.AD_START, e.data);
        this.videoElement.style.display = 'none';
        this.adsRenderingDiv.style.display = 'block';
    }

    private onEnd (e) {
        this.videoElement.style.display = 'block';
        this.adsRenderingDiv.style.display = 'none';
        this.sendEvent(PlayerEvents.AD_END);
    }

    private onAddElement(e) {
        console.log("adsplayer - " + e.type + " / " + e.data.type);
    }
    
    private onRemoveElement(e) {
        console.log("adsplayer - " + e.type + " / " + e.data.type);
    }

    private onPlay(e) {
        console.log("adsplayer - " + e.type);
    }
    
    private onPause(e) {
        console.log("adsplayer - " + e.type);
    }

    private onClick(e) {
        console.log("adsplayer - " + e.type);
        // if (e.data.uri) {
        //     window.open(e.data.uri);
        // }
    }

    private sendEvent (eventType: PlayerEvents, data?: object) {
        let event = document.createEvent("CustomEvent");
        event.initCustomEvent(eventType, false, false, data);
        this.videoElement.dispatchEvent(event);
    }

    // #endregion PRIVATE FUNCTIONS

}
