import { IPlugin } from './IPlugin'
import { PluginType, StreamInfo } from '../Types'
import { IPlayer } from '../IPlayer'
import * as MetricsAgent from 'metricsagent.js';

const DEFAULT_SETTINGS = {
    name: 'Prisme (local)',
    activationUrl: '',
    serverUrl: 'http://localhost:3004/metrics',
    enable: true,
    dbServerUrl: '',
    collector: 'OnePlayerCollector',
    formatter: 'Prisme',
    sendingTime: 10000
};

/**
 * metricsagent.js plugin
 */

export class MetricsPlugin implements IPlugin {
    // #region MEMBERS
    // --------------------------------------------------

    // MetricsAgent instance
    private metricsAgent;

    // #endregion MEMBERS
    // --------------------------------------------------

    // #region CONSTRUCTOR
    // --------------------------------------------------

    /**
     * Constructor.
     */
    constructor () {
    }
    // #endregion CONSTRUCTOR

    // #region PUBLIC FUNCTIONS
    // --------------------------------------------------

    // #region SETUP
    // --------------------------------------------------

    public getType (): string {
        return PluginType.METRICS
    }

    public getVersion (): string {
        this.checkInitialized();
        return 'metricsagent.js_' + this.metricsAgent.getVersion();
    }

    public init (player: IPlayer, videoElement: HTMLMediaElement, config: object) {

        // Get default settings and update with provided settings
        let settings = DEFAULT_SETTINGS;
        for(var key in config) settings[key] = config[key];

        this.metricsAgent = new MetricsAgent(settings);
        this.metricsAgent.init(player, videoElement);
    }

    public enableLogs (enable: boolean) {
    }  

    public reset() {
        this.checkInitialized();
        this.metricsAgent.destroy();
    }

    // #endregion SETUP
    // --------------------------------------------------

    // #region PLUGIN CONTROL
    // --------------------------------------------------

    /**
     * Load/open a video stream.
     * @param {object} stream - video stream properties object such url, startTime, prodData ...
     */    
    public load (stream: StreamInfo): Promise<any> {
        return new Promise<any>((resolve, reject) => {
            this.metricsAgent.load(stream);
            resolve();
        });
    }

    /**
     * Stops/reset the playback of the video stream.
     */
    public stop () {
        this.metricsAgent.reset();
    }

    // #endregion PLUGIN CONTROL
    // --------------------------------------------------
    
    

    // #region PRIVATE FUNCTIONS
    // --------------------------------------------------

    private checkInitialized() {
        if (!this.metricsAgent) {
            throw 'Metrics plugin not initialized';
        } 
    }

    // #endregion PRIVATE FUNCTIONS

}
