import * as dashjs from 'dashjs';

export class TTMLController {
    // #region CONSTRUCTOR
    // --------------------------------------------------

    /**
     * Constructor.
     */
    constructor () {
    }
    // #endregion CONSTRUCTOR

    /**
     * Initialize.
     * @param mediaPlayer the dash.js player
     */
    init (mediaPlayer) {
        mediaPlayer.on(dashjs.MediaPlayer.events.TTML_TO_PARSE, this.onTTMLPreProcess, this);
    }

    // #region PRIVATE METHODS
    // --------------------------------------------------
    private onTTMLPreProcess (ttmlSubtitles) {
        if (!ttmlSubtitles || !ttmlSubtitles.data) {
            return;
        }

        ttmlSubtitles.data = ttmlSubtitles.data.replace(/http:\/\/www.w3.org\/2006\/10\/ttaf1/gi, 'http://www.w3.org/ns/ttml');
        ttmlSubtitles.data = ttmlSubtitles.data.replace(/[\w]+:region/gi, 'region');
        ttmlSubtitles.data = ttmlSubtitles.data.replace(/[\w]+:begin/gi, 'begin');
        ttmlSubtitles.data = ttmlSubtitles.data.replace(/[\w]+:end/gi, 'end');
        ttmlSubtitles.data = ttmlSubtitles.data.replace(/[\w]+:style/gi, 'style');
    }
    // #endregion PRIVATE METHODS
}