import { PlayerType, StreamInfo, MediaType, TrackInfo, PluginType, PlayerEvents } from './Types'
import { IPlayer } from './IPlayer'
import { PlayerErrorCode, PlayerErrorMessage } from './Errors'
import { IPlugin } from './plugins/IPlugin'
// #if process.env.METRICS === "true"
import { MetricsPlugin } from './plugins/MetricsPlugin'
// #endif
// #if process.env.ADINSERTION === "true"
import { AdsPlugin } from './plugins/AdsPlugin'
// #endif
import { PlaybackSpeedController } from './tools/PlaybackSpeedController'
import { DebugController } from './tools/DebugController'


const DEFAULT_MEDIA_SETTINGS_KEY = 'oneplayerjs_media_settings';

/**
 * Default player implementation
 */
export abstract class DefaultPlayer implements IPlayer {
    // #region MEMBERS
    // --------------------------------------------------

    // <video> / HTMLMediaElement
    protected videoElement: HTMLMediaElement;

    // The input stream
    protected stream: StreamInfo;

    // Track settings caching state
    protected storeMediaSettings: boolean;

    // Track settings key
    protected mediaSettingsKey: string;

    // Playback speed controller
    protected playbackSpeedController: PlaybackSpeedController;

    // Debug controller
    protected debugController: DebugController;

    // Plugins
    protected plugins: IPlugin[] = [];

    // Logs state
    protected logsEnabled: boolean;

    // #endregion MEMBERS
    // --------------------------------------------------

    // #region CONSTRUCTOR
    // --------------------------------------------------

    /**
     * Constructor.
     */
    constructor () {
    }
    // #endregion CONSTRUCTOR

    // #region PUBLIC FUNCTIONS
    // --------------------------------------------------

    // #region SETUP
    // --------------------------------------------------

    public abstract getVersion (): string;

    public abstract getPlayerType (): PlayerType;

    public init (videoElement: HTMLElement, ttmlRenderingDiv?: HTMLDivElement): Promise<any> {
        if (!(videoElement instanceof HTMLMediaElement)) {
            throw new Error('video element must be instance of HTMLMediaElement');
        }
        this.videoElement = videoElement as HTMLMediaElement;
        this.stream = null;
        this.storeMediaSettings = false;
        this.mediaSettingsKey = DEFAULT_MEDIA_SETTINGS_KEY;
        this.playbackSpeedController = new PlaybackSpeedController();
        this.playbackSpeedController.init(this.videoElement);
        this.debugController = new DebugController(this, this.videoElement);
        this.logsEnabled = false;

        return Promise.resolve();
    }

    abstract setConfig (params: object);

    public enableLogs (enable: boolean) {
        this.logsEnabled = enable;
        for (let i = 0; i < this.plugins.length; i++) {
            this.plugins[i].enableLogs(enable);
        }
    }

    public enableLastMediaSettingsCaching (enable: boolean, key = DEFAULT_MEDIA_SETTINGS_KEY) {
        this.storeMediaSettings = enable;
        if (key && key.length > 0) {
            this.mediaSettingsKey = key;
        }
        if (this.storeMediaSettings) {
            this.applyMediaSettings();
        } else {
            this.removeMediaSettings();
        }
    }

    abstract addEventListener (type: string, listener: any, scope?: object);

    abstract removeEventListener (type: string, listener: any, scope?: object);

    public reset () {
        this.stop();
        this.debugController.reset();
        // Reset plugins
        for (let i = 0; i < this.plugins.length; i++) {
            this.plugins[i].reset();
        }                
    }

    // #endregion SETUP
    // --------------------------------------------------

    // #region PLAYBACK CONTROL
    // --------------------------------------------------

    public load (stream: StreamInfo, loadPlugins = true) {
        // Stop previous stream
        if (this.stream) {
            this.stop();
        }

        // Apply media settings from local storage
        this.applyMediaSettings();

        if (!loadPlugins) {
            this.loadStream(stream);
            return;
        }

        let pluginLoadPromises: Promise<any>[] = [];
        for (let i = 0; i < this.plugins.length; i++) {
            pluginLoadPromises.push(this.plugins[i].load(stream));
        }

        Promise.all(pluginLoadPromises).then((values) => {
            // In case AdsPlugin encounter a pre-roll ad, then do not start stream playback
            if (values.indexOf('preroll') === -1) {
                this.loadStream(stream);
            }
        }).catch((e) => {
            this.loadStream(stream);
        });
    }

    abstract play ();
    abstract pause ();
    abstract seek (time: number);

    public setPlaybackSpeed (speed: number) {
        this.playbackSpeedController.setSpeed(speed);
    }

    public getPlaybackSpeed () : number {
        return this.playbackSpeedController.getSpeed();
    }

    public stop (reset = true, stopPlugins = true) {

        if (reset) {
            this.stream = null;
            this.playbackSpeedController.reset();
            this.debugController.stop();
        } else {
            this.playbackSpeedController.setSpeed(1);
        }

        if (stopPlugins) {
            // Call stop on plugins
            for (let i = 0; i < this.plugins.length; i++) {
                this.plugins[i].stop();
            }        
        }
    }

    // #endregion PLAYBACK CONTROL
    // --------------------------------------------------
    
    // #region DURATION / TIME
    // --------------------------------------------------

    abstract getDuration () : number;
    abstract isLive ();
    abstract getTime () : number;

    // #endregion DURATION / TIME
    // --------------------------------------------------

    // #region AUDIO/SUBTITLES TRACKS
    // --------------------------------------------------

    /**
     * Returns the list of tracks for a given media type
     * @returns {object} the list of {@link TrackInfo} for each media type
     */
    public getTracks (type: MediaType): object {
        let tracks = {};
        tracks[MediaType.Video] = this.getTracksForType(MediaType.Video);
        tracks[MediaType.Audio] = this.getTracksForType(MediaType.Audio);
        tracks[MediaType.Text] = this.getTracksForType(MediaType.Text);
        return tracks;
    }

    abstract getTracksForType (type: MediaType): TrackInfo[];
    abstract getSelectedTrackForType (type: MediaType): TrackInfo;
    abstract getQualityForType (type: MediaType): number;
    abstract getBufferLevelForType (type: MediaType): number;
    abstract selectTrackForType (type: MediaType, id: string);
    abstract selectTrack (track: TrackInfo);
    abstract setDefaultAudioLanguage (lang: string);
    abstract setDefaultTextLanguage (lang: string);
    abstract enableText (enable: boolean);
    abstract isTextEnabled () : boolean;
    abstract setDefaultTextEnabled (enable: boolean);

    // #endregion AUDIO/SUBTITLES TRACKS
    // --------------------------------------------------    

    // #region PLUGIN
    // --------------------------------------------------

    public addPlugin (type: PluginType, config?: object) {
        let plugin;
        switch (type) {
            // #if process.env.METRICS === "true"
            case PluginType.METRICS:
                plugin = new MetricsPlugin();
                break;
            // #endif
            // #if process.env.ADINSERTION === "true"
            case PluginType.ADINSERTION:
                plugin = new AdsPlugin();
                break;
            // #endif
            default:
                throw new Error('Unknown plugin type: ' + type)
        }
        plugin.init(this, this.videoElement, config);
        plugin.enableLogs(this.logsEnabled);
        this.plugins.push(plugin);
    }

    // #endregion PLUGIN
    // --------------------------------------------------

    // #region DEBUG
    // --------------------------------------------------

    public showDebug (show: boolean, debugRenderingDiv?: HTMLDivElement) {
        this.debugController.showDebugPanel(show, debugRenderingDiv);
    }

    // #endregion DEBUG
    // --------------------------------------------------

    // #endregion PUBLIC FUNCTIONS
    // --------------------------------------------------

    // #region PROTECTED FUNCTIONS
    // --------------------------------------------------

    protected loadStream(stream: StreamInfo) {
        this.stream = stream;
        this.debugController.start(stream);
    }

    protected abstract limitToLowestBitrate(state: boolean);

    protected sendErrorEvent (code: PlayerErrorCode, extMessage?: string, data?: object) {
        let event = document.createEvent("CustomEvent");
        let error = {
            code: code,
            message: PlayerErrorMessage[code],
            extMessage: extMessage,
            data: data
        }
        event.initCustomEvent(PlayerEvents.PLAYER_ERROR, false, false, error);
        this.videoElement.dispatchEvent(event);
    }    

    protected storeDefaultLanguage (type: MediaType, lang: string) {
        if (!this.storeMediaSettings) {
            return;
        }
        let settings = this.getMediaSettings();
        if (!settings[type]) {
            settings[type] = {};
        }
        settings[type]['lang'] = lang;
        localStorage.setItem(this.mediaSettingsKey, JSON.stringify(settings));
    }

    protected storeTextState (enable: boolean) {
        if (!this.storeMediaSettings) {
            return;
        }
        let settings = this.getMediaSettings();
        if (!settings[MediaType.Text]) {
            settings[MediaType.Text] = {};
        }
        settings[MediaType.Text]['enable'] = enable;
        localStorage.setItem(this.mediaSettingsKey, JSON.stringify(settings));
    }

    // #endregion PROTECTED FUNCTIONS
    // --------------------------------------------------

    // #region PRIVATE FUNCTIONS
    // --------------------------------------------------

    private applyMediaSettings () {
        let storedSettings = localStorage.getItem(this.mediaSettingsKey);
        if (!storedSettings) {
            return;
        }
        try {
            let settings = JSON.parse(storedSettings);
            if (settings['audio'] !== undefined && settings['audio']['lang'] !== undefined) {
                this.setDefaultAudioLanguage(settings['audio']['lang']);
            }
            if (settings['text'] !== undefined && settings['text']['lang'] !== undefined) {
                this.setDefaultTextLanguage(settings['text']['lang']);
            }
            if (settings['text'] !== undefined && settings['text']['enable'] !== undefined) {
                this.setDefaultTextEnabled(settings['text']['enable']);
            }
        } catch (e) {
            console.log(e);
        }
    }

    private removeMediaSettings () {
        localStorage.removeItem(this.mediaSettingsKey);
    }

    private getMediaSettings () {
        let settings = {};
        let storedSettings = localStorage.getItem(this.mediaSettingsKey);
        if (storedSettings) {
            try {
                settings = JSON.parse(storedSettings);
            } catch (e) {
                return settings;
            }
        }
        return settings;
    }

    // #endregion PRIVATE FUNCTIONS
}
