import { OnePlayer } from '../OnePlayer'
import { IPlayer } from '../IPlayer'
import { StreamInfo, ServiceType, MediaType, Quality, PlayerType } from '../Types'
import { DashJSPlayer } from '../dashjs/DashJSPlayer'

let html = require('./DebugComponent.html');
let css = require('./DebugComponent.css');

const POLL_INTERVAL = 500; // 500ms
const METRICS_COUNT = 120; // 2 per seconds, 60sec

declare global {
    interface Window { Chartist: any; }
}

window.Chartist = window.Chartist || {};

export class DebugPanel {

    // #region MEMBERS
    // --------------------------------------------------

    private videoElement: HTMLMediaElement;
    private player: IPlayer;

    private containerDiv: HTMLElement;
    private debugDiv: HTMLDivElement;

    // Listeners
    private stateChangedListener;
    private pauseListener;
    private qualityRenderedListener;
   
    // Data
    private version = '';
    private protocol: string = '';
    private streamType: string = '';
    private playbackState: string = '';
    private playbackTime: number = 0;
    private duration: number = 0;
    private jitter: number = 0;
    private videoBufferSizes: number[];
    private audioBufferSizes: number[];
    private videoBuffer: number = 0;
    private audioBuffer: number = 0;
    private bufferTarget: number = 0;
    private videoQualities: number[];
    private videoBitrate: number = 0;
    private connectionSpeeds: number[];
    private connectionSpeed: number = 0;
    private playbackQuality: number = -1;

    // Charts
    private videoBufferChart: Chartist.IChartistLineChart;
    private audioBufferChart: Chartist.IChartistLineChart;
    private videoBitrateChart: Chartist.IChartistLineChart;
    private connectionSpeedChart: Chartist.IChartistLineChart;

    // Interval for polling metrics
    private pollInterval;

    // Live metrics used to determine jitter
    private liveStartTime: number;
    private liveStartPlaybackTime: number;

    // #endregion MEMBERS

    // #region CONSTRUCTOR
    // --------------------------------------------------

    /**
     * Constructor.
     */
    constructor (player: IPlayer, video: HTMLElement) {
        this.player = player;
        this.videoElement = video as HTMLMediaElement;
        this.containerDiv = null;
        this.debugDiv = null;
        this.pollInterval = null;

        this.videoBufferSizes = [];
        this.audioBufferSizes = [];
        this.videoQualities = [];
        this.connectionSpeeds = [];

        this.stateChangedListener = this.onStateChanged.bind(this);
        this.pauseListener = this.onPause.bind(this);
        this.qualityRenderedListener = this.onQualityRendered.bind(this);

        this.registerEvents();

        this.init();
    }

    // #endregion CONSTRUCTOR    
    // --------------------------------------------------

    // #region PUBLIC METHODS
    // --------------------------------------------------

    public reset() {
        this.unregisterEvents();
        this.show(false);
    }

    public start (stream: StreamInfo) {
        this.init();
        this.protocol = stream.protocol || '';
        this.streamType = stream.type ? (stream.type === ServiceType.LIVE ? 'live' : 'static') : '';
        this.render();
    }

    public stop() {
        this.stopMetricsPolling();
        this.init();
        this.render();
    }

    public show (show: boolean, debugRenderingDiv?: HTMLDivElement) {
        if (!show && this.debugDiv !== null) {
            this.containerDiv.removeChild(this.debugDiv);
            this.debugDiv = null;
        } else if (show && this.debugDiv === null) {
            // Create debug panel and append it to provided container or to <video> parent element
            this.containerDiv = debugRenderingDiv ? debugRenderingDiv : this.videoElement.parentElement;
            this.createDebugPanel();
            this.render();
        }
    }

    // #endregion PUBLIC METHODS
    // --------------------------------------------------

    // #region PRIVATE METHODS
    // --------------------------------------------------

    private registerEvents () {
        this.videoElement.addEventListener('loadedmetadata', this.stateChangedListener);
        this.videoElement.addEventListener('seeking', this.stateChangedListener);
        this.videoElement.addEventListener('seeked', this.stateChangedListener);
        this.videoElement.addEventListener('playing', this.stateChangedListener);
        this.videoElement.addEventListener('waiting', this.stateChangedListener);
        this.videoElement.addEventListener('pause', this.pauseListener);

        if (this.player.getPlayerType() === PlayerType.DASHJS) {
            this.player.addEventListener('qualityChangeRendered', this.qualityRenderedListener);
        }
    }

    private unregisterEvents () {
        this.videoElement.removeEventListener('loadedmetadata', this.stateChangedListener);
        this.videoElement.removeEventListener('seeking', this.stateChangedListener);
        this.videoElement.removeEventListener('seeked', this.stateChangedListener);
        this.videoElement.removeEventListener('playing', this.stateChangedListener);
        this.videoElement.removeEventListener('waiting', this.stateChangedListener);
        this.videoElement.removeEventListener('pause', this.pauseListener);

        if (this.player.getPlayerType() === PlayerType.DASHJS) {
            this.player.removeEventListener('qualityChangeRendered', this.qualityRenderedListener);
        }
    }

    private init() {
        this.version = 'oneplayer.js v' + OnePlayer.getVersion() + ((this.player.getVersion().length > 0) ? (' / ' + this.player.getVersion()) : '');
        this.protocol = '';
        this.streamType = '';
        this.playbackState = '';
        this.playbackTime = 0;
        this.duration = 0;
        this.jitter = 0;
        this.videoBufferSizes = [0];
        this.audioBufferSizes = [0];
        this.videoBuffer = 0;
        this.audioBuffer = 0;
        this.bufferTarget = 0;
        this.videoQualities = [0];
        this.videoBitrate = 0;
        this.connectionSpeeds = [0];
        this.connectionSpeed = 0;
        this.playbackQuality = -1;

        this.liveStartTime = 0;
        this.liveStartPlaybackTime = 0;
    }

    private onStateChanged (e) {
        this.playbackState = e.type;

        if (e.type === 'loadedmetadata' || e.type === 'playing') {
            this.startMetricsPolling();
        }

        if (e.type === 'playing' && this.player.isLive()) {
            this.liveStartTime = new Date().getTime();
            this.liveStartPlaybackTime = this.videoElement.currentTime;
        }
    }

    private onPause (e) {
        this.playbackState = 'paused';
        this.render();
        this.stopMetricsPolling();
    }

    private onQualityRendered (e) {
        if (e.mediaType !== MediaType.Video) {
            return;
        }
        this.playbackQuality = e.newQuality;
    }

    private startMetricsPolling () {
        if (this.pollInterval !== null) {
            return;
        }
        this.updateMetrics();
        this.pollInterval = setInterval(() => {
            this.updateMetrics();
        }, POLL_INTERVAL);
    }

    private stopMetricsPolling () {
        if (this.pollInterval === null) {
            return;
        }
        clearInterval(this.pollInterval);
        this.pollInterval = null;
    }

    private updateMetrics () {
        let time = new Date().getTime();

        this.streamType = this.player.isLive() ? 'live' : 'static';
        this.playbackTime = this.videoElement.currentTime;
        this.duration = this.videoElement.duration;

        if (this.liveStartTime > 0) {
            this.jitter = ((time - this.liveStartTime) / 1000) - (this.playbackTime - this.liveStartPlaybackTime);
        }

        if (this.player.getPlayerType() === PlayerType.DASHJS) {
            this.updateDashMetrics();
        }

        this.render();
    }

    private updateDashMetrics () {
        // Get dash.js metrics for audio and video
        let mediaPlayer: DashJSPlayer = this.player as DashJSPlayer;

        // Get video metrics
        let videoMetrics = mediaPlayer.getMetricsFor(MediaType.Video);

        // Update buffer target (for video and audio based on video buffer target)
        if (videoMetrics && videoMetrics.BufferState.length > 0) {
            this.bufferTarget = Math.max(this.bufferTarget, videoMetrics.BufferState[videoMetrics.BufferState.length - 1].target);
        }
        // Update video buffer level
        this.videoBuffer = this.updateBufferLevel(videoMetrics, this.videoBufferSizes, this.videoBufferChart);
        // Update connection speed based on video segments download speed
        this.updateConnectionSpeed(videoMetrics);
        
        // Update audio buffer level
        let audioMetrics = mediaPlayer.getMetricsFor(MediaType.Audio);
        this.audioBuffer = this.updateBufferLevel(audioMetrics, this.audioBufferSizes, this.audioBufferChart);

        this.updateVideoBitrate();
    }

    private updateBufferLevel(metrics, bufferSizes: number[], chart: Chartist.IChartistLineChart) {
        let level = 0;
        if (metrics && metrics.BufferLevel.length > 0) {
            level = metrics.BufferLevel[metrics.BufferLevel.length - 1].level / 1000;
            this.appendChartValue(bufferSizes, level);
        }
        if (this.debugDiv !== null) {
            chart.options.high = this.bufferTarget;
        }
        return level;
    }

    private updateVideoBitrate() {
        let videoTrack = this.player.getSelectedTrackForType(MediaType.Video);
        if (videoTrack === null) {
            return;
        }

        if (this.debugDiv !== null) {
            // Update number of qualities/bitrates
            this.videoBitrateChart.options.high = videoTrack.qualities.length - 1;
        }

        // Update current quality index
        let qualityIndex = this.player.getQualityForType(MediaType.Video);
        this.appendChartValue(this.videoQualities, qualityIndex);

        // Get corresponding bitrate
        let quality: Quality = videoTrack.qualities[qualityIndex];
        this.videoBitrate = quality.bitrate;
    }

    private updateConnectionSpeed(metrics) {

        if (!metrics || metrics.HttpList.length === 0) {
            return;
        }

        let request = metrics.HttpList[metrics.HttpList.length - 1];
        if (request.responsecode !== 200) {
            return;
        }
        let responseTime = new Date(request.tresponse);
        let finishTime = new Date(request._tfinish)
        let downloadTimeInSeconds = (finishTime.getTime() - responseTime.getTime()) / 1000;
        let downloadBytes = request.trace.reduce((a, b) => a + b.b[0], 0);
        this.connectionSpeed = Math.round((8 * downloadBytes) / downloadTimeInSeconds);

        this.appendChartValue(this.connectionSpeeds, this.connectionSpeed);
        if (this.debugDiv !== null) {
            this.connectionSpeedChart.options.high = Math.max(this.connectionSpeedChart.options.high, this.connectionSpeed);
        }
    }

    private appendChartValue(values: number[], value) {
        if (values.length >= METRICS_COUNT) {
            values.shift();
        }
        values.push(value);
    }

    private render () {
        if (!this.debugDiv) {
            return;
        }

        this.debugDiv.querySelector("#oneplayer-debug-version").innerHTML = this.version;

        if (this.streamType !== '') {
            this.debugDiv.querySelector("#oneplayer-debug-duration-jitter-label").innerHTML = this.streamType === 'live' ? 'Jitter:' : 'Duration:';
        }

        this.debugDiv.querySelector("#oneplayer-debug-protocol").innerHTML = this.protocol;
        this.debugDiv.querySelector("#oneplayer-debug-stream-type").innerHTML = this.streamType;
        this.debugDiv.querySelector("#oneplayer-debug-playback-state").innerHTML = this.playbackState;
        this.debugDiv.querySelector("#oneplayer-debug-playback-time").innerHTML = this.playbackTime.toFixed(3) + 's';
        this.debugDiv.querySelector("#oneplayer-debug-duration-jitter").innerHTML = this.streamType === 'live' ? (this.jitter.toFixed(3) + 's'): this.formatDuration(this.duration);
        this.debugDiv.querySelector("#oneplayer-debug-playback-quality").innerHTML = this.getPlaybackQualityInfos();
        this.debugDiv.querySelector("#oneplayer-debug-video-buffer").innerHTML = this.videoBuffer.toFixed(1) + 's';
        this.debugDiv.querySelector("#oneplayer-debug-audio-buffer").innerHTML = this.audioBuffer.toFixed(1) + 's';
        this.debugDiv.querySelector("#oneplayer-debug-video-bitrate").innerHTML = this.formatBitrate(this.videoBitrate);
        this.debugDiv.querySelector("#oneplayer-debug-connection-speed").innerHTML = this.formatBitrate(this.connectionSpeed);

        this.videoBufferChart.update({
            series: [this.videoBufferSizes]
        }, this.videoBufferChart.options);

        this.audioBufferChart.update({
            series: [this.audioBufferSizes]
        }, this.audioBufferChart.options);

        this.videoBitrateChart.update({
            series: [this.videoQualities]
        }, this.videoBitrateChart.options);

        this.connectionSpeedChart.update({
            series: [this.connectionSpeeds]
        }, this.connectionSpeedChart.options);

        // Set debug panel visible
        this.debugDiv.style.visibility = 'visible';
    }

    private formatDuration (duration: number) {
        let s =  duration.toFixed(3) + 's';
        if (duration < 60) {
            return s;
        }
        s += ' (';
        if (duration > 3600) {
            s += Math.floor(duration / 3600) + 'h';
        }
        if (duration > 60) {
            s += Math.floor(duration % 3600 / 60) + 'm';
        }
        s += (duration % 60).toFixed(3) + 's)';
        return s;
    }

    private formatBitrate (bitrate: number) {
        let bitrateInKbps = Math.round(bitrate / 1000);
        if (bitrateInKbps > 1000) {
            return (bitrateInKbps / 1000).toFixed(1) + 'Mb/s';
        }
        return bitrateInKbps + 'Kb/s';
    }

    private getPlaybackQualityInfos () {
        if (this.playbackQuality === -1) {
            return '';
        }

        let videoTrack = this.player.getSelectedTrackForType(MediaType.Video);
        if (videoTrack === null) {
            return '';
        }

        let quality: Quality = videoTrack.qualities[this.playbackQuality];
        let qualityInfos = this.formatBitrate(quality.bitrate);
        if (quality.width && quality.height) {
            qualityInfos += ' - ' + quality.width + 'x' + quality.height;
        }
        qualityInfos += ' (' + (this.playbackQuality + 1) + '/' + videoTrack.qualities.length + ')';
        return qualityInfos;
    }

    private loadChartist () {
        let node = document.createElement('script');
        node.src = 'https://cdnjs.cloudflare.com/ajax/libs/chartist/0.11.0/chartist.js';
        node.type = 'text/javascript';
        node.onload = this.ShowDebug.bind(this);
        document.getElementsByTagName('head')[0].appendChild(node);
    }

    private ShowDebug () {
        // Add debug panel styles
        this.addStyles('oneplayer-debug-styles', css);

        // Create <div> for debug panel
        this.debugDiv = document.createElement('div');
        this.debugDiv.style.visibility = 'hidden';
        this.debugDiv.id = 'oneplayer-debug';
        this.debugDiv.innerHTML = html;

        // Append it to provided container or to <video> parent element
        this.containerDiv.appendChild(this.debugDiv);

        // Create buffer size charts
        let optionsBufferChart: Chartist.ILineChartOptions = {
            high: this.bufferTarget,
            low: 0,
            fullWidth: true,
            showPoint: false,
            showLine: false,
            showArea: true,
            chartPadding: {
                bottom: 0,
                left: 0,
                right: 0,
                top: 0
            },
            axisX: {
                showGrid: false,
                showLabel: false,
                offset: 0
            },
            axisY: {
                showGrid: false,
                showLabel: false,
                offset: 0
            },
            lineSmooth: window.Chartist.Interpolation.none()
        };

        this.videoBufferChart = new window.Chartist.Line('#oneplayer-debug-video-buffer-chart', {
            series: [this.videoBufferSizes]
        }, optionsBufferChart);

        this.audioBufferChart = new window.Chartist.Line('#oneplayer-debug-audio-buffer-chart', {
            series: [this.videoBufferSizes]
        }, optionsBufferChart);

        // Create video bitrate chart
        this.videoBitrateChart = new window.Chartist.Line('#oneplayer-debug-video-bitrate-chart', {
            series: [this.videoQualities]
        }, {
            low: 0,
            fullWidth: true,
            showPoint: false,
            showLine: true,
            showArea: false,
            chartPadding: {
                bottom: 0,
                left: 0,
                right: 0,
                top: 0
            },
            axisX: {
                showGrid: false,
                showLabel: false,
                offset: 0
            },
            axisY: {
                showGrid: false,
                showLabel: false,
                offset: 0
            },
            lineSmooth: window.Chartist.Interpolation.step()
        });

        // Create connection speed chart
        this.connectionSpeedChart = new window.Chartist.Line('#oneplayer-debug-connection-speed-chart', {
            series: [this.connectionSpeeds]
        }, {
            low: 0,
            fullWidth: true,
            showPoint: false,
            showLine: true,
            showArea: false,
            chartPadding: {
                bottom: 0,
                left: 0,
                right: 0,
                top: 0
            },
            axisX: {
                showGrid: false,
                showLabel: false,
                offset: 0
            },
            axisY: {
                showGrid: false,
                showLabel: false,
                offset: 0
            },
            lineSmooth: window.Chartist.Interpolation.simple()
        });
    }

    private createDebugPanel () {
        if(!window.Chartist.Interpolation) {
            this.loadChartist();
        } else {
            this.ShowDebug();
        }
    }

    private addStyles(id: string, css: any) {
        let head = this.getHeadSection();

        // Check if not already existing
        let style = head.querySelector('#' + id);
        if (style) {
            return;
        }

        style = document.createElement('style');
        style.id = id;
        style.innerHTML = css;
        head.appendChild(style);        
    }

    private getHeadSection() {
        // Get <head> section
        let elements = document.getElementsByTagName('head');
        if (elements.length > 0) {
            return elements[0];
        }

        // Create <head> if not existing
        let head = document.createElement('head');
        document.body.appendChild(head);
        return head;
    }

    // #endregion PRIVATE METHODS
    // --------------------------------------------------

}

