import { IPlayer } from '../IPlayer'
import { DefaultPlayer } from '../DefaultPlayer'
import { PlayerType, StreamInfo, MediaType, TrackInfo, AudioKind, TextKind } from '../Types'
import { PlayerErrorCode } from '../Errors'
import { TTMLController } from './TTMLController';
import * as dashjs from 'dashjs';
import { MediaPlayerClass } from 'dashjs';

const DASHJS_PLAYER_NOT_INITIALIZED_ERROR = 'DashJS player not initialized!';

const DEFAULT_SETTINGS = {
    'MSS': {
        'LIVE': {
            liveDelay: 16,
            stableBufferTime: 16,
            bufferTimeAtTopQuality: 16,
            bufferTimeAtTopQualityLongForm: 16,
            lowLatencyEnabled: false,
            keepProtectionMediaKeys: true
        }
    }
};

const MEDIAINFO_TYPE_TO_MEDIATYPE = {
    'video': MediaType.Video,
    'audio': MediaType.Audio,
    'text': MediaType.Text,
    'fragmentedText': MediaType.Text,
    'embeddedText': MediaType.Text
};

/**
 * Dash.js player
 */
export class DashJSPlayer extends DefaultPlayer implements IPlayer {
    // #region MEMBERS
    // --------------------------------------------------

    // Dash.js player
    private mediaPlayer: MediaPlayerClass;

    // Default settings
    private defaultSettings: object = {};

    // Audio settings
    private audioSettings: object = {};

    // Download error
    private downloadError;

    // TTML Controller in charge of preproccessing TTML datas before parsing
    private ttmlController: TTMLController;

    // #endregion MEMBERS
    // --------------------------------------------------

    // #region CONSTRUCTOR
    // --------------------------------------------------

    /**
     * Constructor.
     */
    constructor () {
        super();
        this.mediaPlayer = dashjs.MediaPlayer().create();
        this.mediaPlayer.on(dashjs.MediaPlayer.events.ERROR, this.onError, this);
        this.mediaPlayer.on(dashjs.MediaPlayer.events.PLAYBACK_PLAYING, this.onPlaybackPlaying, this);
        this.mediaPlayer.on(dashjs.MediaPlayer.events.PLAYBACK_SEEKING, this.onPlaybackSeeking, this);
        this.mediaPlayer.on(dashjs.MediaPlayer.events.PLAYBACK_WAITING, this.onPlaybackWaiting, this);

        this.ttmlController = new TTMLController();
        this.ttmlController.init(this.mediaPlayer);

        this.getDefaultSettings();
    }
    // #endregion CONSTRUCTOR

    // #region PUBLIC FUNCTIONS
    // --------------------------------------------------

    // #region SETUP
    // --------------------------------------------------

    public getVersion (): string {
        this.checkInitialized();
        return 'dash.js_' + this.mediaPlayer.getVersion();
    }

    public getPlayerType (): PlayerType {
        return PlayerType.DASHJS;
    }

    public init (videoElement: HTMLElement, ttmlRenderingDiv?: HTMLDivElement) : Promise<any> {
        this.checkInitialized();
        super.init(videoElement, ttmlRenderingDiv);

        // Attach video element
        this.mediaPlayer.initialize(this.videoElement, undefined, true);

        // Initialize text (subtitles/captions) rendering
        if (ttmlRenderingDiv) {
            this.mediaPlayer.attachTTMLRenderingDiv(ttmlRenderingDiv);
        } else {
            ttmlRenderingDiv = document.createElement('div');
            if (this.videoElement.parentNode) {
                this.videoElement.parentNode.appendChild(ttmlRenderingDiv);
                this.mediaPlayer.attachTTMLRenderingDiv(ttmlRenderingDiv);
            }
        }

        // Disable media settings caching in local storage
        this.mediaPlayer.enableLastMediaSettingsCaching(false);

        // Enable last bitrate caching in local storage
        this.mediaPlayer.enableLastBitrateCaching(true);

        this.mediaPlayer.setLowLatencyEnabled(false);

        return Promise.resolve();
    }

    public setConfig (params: object) {
        this.checkInitialized();
        // Store given settings in default settings
        for (let key in params) {
            this.defaultSettings[key] = params[key];
        }
        this.applySettings(params);
    }
 
    public enableLogs (enable: boolean) {
        super.enableLogs(enable);
        this.checkInitialized();
        this.mediaPlayer.getDebug().setLogToBrowserConsole(enable);
    }
  
    public addEventListener (type: string, listener: any, scope?: object) {
        this.checkInitialized();
        this.mediaPlayer.on(type, listener, scope);
    }

    public removeEventListener (type: string, listener: any, scope?: object) {
        this.checkInitialized();
        this.mediaPlayer.off(type, listener, scope);
    }

    public reset() {
        super.reset();
        this.checkInitialized();
        this.mediaPlayer.reset();
    }

    public getMetricsFor(type: MediaType) {
        this.checkInitialized();
        return this.mediaPlayer.getMetricsFor(this.getType(type));        
    }

    // #endregion SETUP
    // --------------------------------------------------

    // #region PLAYBACK CONTROL
    // --------------------------------------------------

    public play () {
        this.checkInitialized();
        this.mediaPlayer.play();
    }

    public pause () {
        this.checkInitialized();
        this.mediaPlayer.pause();
    }

    public seek (time: number) {
        this.checkInitialized();
        this.mediaPlayer.seek(time);
    }

    public stop (reset = true, stopPlugins?: boolean) {
        super.stop(reset, stopPlugins);
        this.checkInitialized();
        if (reset || this.mediaPlayer.isDynamic()) {
            this.mediaPlayer.attachSource(null);
        } else {
            this.pause();
            this.seek(0);
        }
    }

    // #endregion PLAYBACK CONTROL
    // --------------------------------------------------
    
    // #region DURATION / TIME
    // --------------------------------------------------

    public getDuration () : number {
        this.checkInitialized();
        if (!this.mediaPlayer.isReady()) {
            return 0;
        }
        return this.mediaPlayer.duration();
    }

    public isLive () {
        this.checkInitialized();
        return this.mediaPlayer.isDynamic();
    }

    public getTime () : number {
        this.checkInitialized();
        if (!this.mediaPlayer.isReady()) {
            return 0;
        }
        return this.mediaPlayer.time(undefined);  
    }

    // #endregion DURATION / TIME
    // --------------------------------------------------

    // #region AUDIO/SUBTITLES TRACKS
    // --------------------------------------------------

    public getTracksForType (type: MediaType): TrackInfo[] {
        this.checkInitialized();

        let tracksInfo: TrackInfo[] = [];
        let mediaType = this.getType(type);
        let selectedTrack = this.mediaPlayer.getCurrentTrackFor(mediaType);
        let tracks = this.mediaPlayer.getTracksFor(mediaType);
        for (let i = 0; i < tracks.length; i++) {
            tracksInfo.push(this.mediaInfoToTrackInfo(tracks[i], tracksInfo.length, tracks[i] === selectedTrack));
        }

        return tracksInfo;
    } 

    public getSelectedTrackForType (type: MediaType): TrackInfo {
        this.checkInitialized();

        let trackInfos = this.getTracksForType(type);
        for (let i = 0; i < trackInfos.length; i++) {
            if (trackInfos[i].selected) {
                return trackInfos[i];
            }
        }
        return null;
    }

    public getQualityForType (type: MediaType): number {
        this.checkInitialized();
        return this.mediaPlayer.getQualityFor(this.getType(type));
    }

    public getBufferLevelForType (type: MediaType): number {
        this.checkInitialized();
        return this.mediaPlayer.getBufferLength(this.getType(type));
    }

    public selectTrackForType (type: MediaType, id: string) {
        this.checkInitialized();

        let mediaType = this.getType(type);
        let tracks = this.mediaPlayer.getTracksFor(mediaType);
        if (!tracks || tracks.length === 0) {
            return;
        }
        let track = (tracks.filter((mediaInfo, idx) => {
            return (mediaInfo.id === undefined) ? (idx === parseInt(id)) : (mediaInfo.id === id);
        }))[0];

        this.storeDefaultLanguage(type, track.lang);
        this.mediaPlayer.setCurrentTrack(track);
    } 

    public selectTrack (track: TrackInfo) {
        this.selectTrackForType(track.mediaType, track.id);
    }

    public setDefaultAudioLanguage (lang: string) {
        this.checkInitialized();
        this.audioSettings['lang'] = lang;
    }

    public setDefaultTextLanguage (lang: string) {
        this.checkInitialized();
        this.mediaPlayer.setTextDefaultLanguage(lang);
    }

    public enableText (enable: boolean) {
        this.checkInitialized();
        this.storeTextState(enable);
        this.mediaPlayer.enableText(enable);
    }

    public isTextEnabled () : boolean {
        this.checkInitialized();
        return this.mediaPlayer.isTextEnabled();
    }

    public setDefaultTextEnabled (enable: boolean) {
        this.checkInitialized();
        this.mediaPlayer.setTextDefaultEnabled(enable);
    }

    // #endregion AUDIO/SUBTITLES TRACKS
    // --------------------------------------------------    

    // #endregion PUBLIC FUNCTIONS
    // --------------------------------------------------

    // #region PROTECTED FUNCTIONS
    // --------------------------------------------------

    protected loadStream (stream: StreamInfo) {
        super.loadStream(stream);
        this.checkInitialized();

        if (stream.protData) {
            this.mediaPlayer.setProtectionData(stream.protData);
        }

        this.downloadError = null;

        this.mediaPlayer.on(dashjs.MediaPlayer.events.STREAM_INITIALIZED, this.onStreamInitialized, this);

        // Restore default settings
        this.applySettings(this.defaultSettings);

        // Apply settings according to protocol and service type
        if (stream.protocol && stream.type) {
            if (DEFAULT_SETTINGS[stream.protocol] && DEFAULT_SETTINGS[stream.protocol][stream.type]) {
                this.applySettings(DEFAULT_SETTINGS[stream.protocol][stream.type]);
            }    
        }

        this.mediaPlayer.attachSource(stream.url);

        // Set audio initial settings after attachSource(), since attachSource() does reset previous stream media settings
        this.mediaPlayer.setInitialMediaSettingsFor('audio', this.audioSettings);
    }

    protected limitToLowestBitrate(state: boolean) {
        if (state === true) {
            // Switch to lowest quality
            let videoInfo = this.getSelectedTrackForType(MediaType.Video);
            let audioInfo = this.getSelectedTrackForType(MediaType.Audio);
            this.mediaPlayer.setMaxAllowedBitrateFor('video', Math.floor(videoInfo.qualities[0].bitrate / 1000) + 1);
            this.mediaPlayer.setMaxAllowedBitrateFor('audio', Math.floor(audioInfo.qualities[0].bitrate / 1000) + 1);
        } else {
            // Reset max allowed bit rate
            this.mediaPlayer.setMaxAllowedBitrateFor('video', NaN);
            this.mediaPlayer.setMaxAllowedBitrateFor('audio', NaN);            
        }
    }

    // #endregion PROTECTED FUNCTIONS

    // #region PRIVATE FUNCTIONS
    // --------------------------------------------------

    private checkInitialized() {
        if (!this.mediaPlayer) {
            throw DASHJS_PLAYER_NOT_INITIALIZED_ERROR;
        } 
    }

    private getDefaultSettings () {
        this.defaultSettings['liveDelay'] = this.mediaPlayer.getLiveDelay();
        this.defaultSettings['stableBufferTime'] = this.mediaPlayer.getStableBufferTime();
        this.defaultSettings['bufferTimeAtTopQuality'] = this.mediaPlayer.getBufferTimeAtTopQuality();
        this.defaultSettings['bufferTimeAtTopQualityLongForm'] = this.mediaPlayer.getBufferTimeAtTopQualityLongForm();
        this.defaultSettings['lowLatencyEnabled'] = this.mediaPlayer.getLowLatencyEnabled();
    }

    private applySettings (settings) {
        if (settings.liveDelay) {
            this.mediaPlayer.setLiveDelay(settings.liveDelay);
        }
        if (settings.stableBufferTime) {
            this.mediaPlayer.setStableBufferTime(settings.stableBufferTime);
        }
        if (settings.bufferTimeAtTopQuality) {
            this.mediaPlayer.setBufferTimeAtTopQuality(settings.bufferTimeAtTopQuality);
        }
        if (settings.bufferTimeAtTopQualityLongForm) {
            this.mediaPlayer.setBufferTimeAtTopQualityLongForm(settings.bufferTimeAtTopQualityLongForm);
        }
        if (settings.lowLatencyEnabled) {
            this.mediaPlayer.setLowLatencyEnabled(settings.lowLatencyEnabled);
        }
        if (settings.keepProtectionMediaKeys) {
            this.mediaPlayer.keepProtectionMediaKeys(settings.keepProtectionMediaKeys);
        }
        if (settings.minVideoBitrate) {
            this.mediaPlayer.setMinAllowedBitrateFor('video', settings.minVideoBitrate);
        }
    }

    private mediaInfoToTrackInfo (mediaInfo: dashjs.MediaInfo, index: number, selected: boolean) {
        let trackInfo: TrackInfo = {
            mediaType: MEDIAINFO_TYPE_TO_MEDIATYPE[mediaInfo.type],
            id: (mediaInfo.id !== undefined) ? mediaInfo.id : index.toString(),
            selected: selected,
            codec: mediaInfo.codec,
            mimeType: mediaInfo.mimeType,
            lang: mediaInfo.lang,
            qualities: mediaInfo.bitrateList.map(function (item) {
                return {
                    bitrate: item.bandwidth,
                    width: item.width,
                    height: item.height
                }
            })
        };

        // For audio tracks, determine track kind value
        if (trackInfo.mediaType === MediaType.Audio && mediaInfo.roles) {
            if (mediaInfo.roles.indexOf('main') !== -1) {
                trackInfo.kind = AudioKind.Main;
            }
            if (mediaInfo.roles.indexOf('dub') !== -1) {
                trackInfo.kind = AudioKind.Translation;
            }
            if (mediaInfo.roles.indexOf('alternate') !== -1) {
                trackInfo.kind = (mediaInfo.accessibility && mediaInfo.accessibility.indexOf('2') !== -1) ? AudioKind.Clean : AudioKind.Alternative;
            }
            if (mediaInfo.roles.indexOf('commentary') !== -1) {
                trackInfo.kind = AudioKind.Commentary;
            }
        }

        // For text tracks, determine track kind value
        if (trackInfo.mediaType === MediaType.Text && mediaInfo.roles) {
            if (mediaInfo.roles.indexOf('main') !== -1) {
                trackInfo.kind = (mediaInfo.accessibility && mediaInfo.accessibility.indexOf('2') !== -1) ? TextKind.Descriptions : TextKind.Captions;
            } else if (mediaInfo.roles.indexOf('alternate') !== -1) {
                trackInfo.kind = TextKind.Subtitles;
            }
        }

        return trackInfo;
    }

    private onStreamInitialized (e) {
        this.mediaPlayer.off(dashjs.MediaPlayer.events.STREAM_INITIALIZED, this.onStreamInitialized, this);

        // If start time is specified then seek at start time
        if (this.stream.startTime) {
            this.mediaPlayer.seek(this.stream.startTime);
        }
    }

    private getResponseText (response) {
        return response.responseType === 'text' ? response.responseText : ''
    }

    private getType (dashType) {
        return dashType === MediaType.Text ? 'fragmentedText' : dashType
    }

    private onPlaybackPlaying (e) {
        this.downloadError = null;
    }

    private onPlaybackSeeking (e) {
        this.downloadError = null;
    }

    private onPlaybackWaiting (e) {
        this.processDownloadError();
    }

    private processDownloadError () {
        if (this.downloadError === null) {
            return;
        }

        let mediaType = MEDIAINFO_TYPE_TO_MEDIATYPE[this.downloadError.data.request.mediaType];

        // Consider only audio and video segments download error
        if (mediaType !== MediaType.Video && mediaType !== MediaType.Audio) {
            return;
        }

        // Check readyState to know if enough data to enable playback
        if (this.videoElement.readyState > 2 && this.videoElement.playbackRate > 0) {
            return;
        }

        // Trigger error 
        this.sendErrorEvent(PlayerErrorCode.DOWNLOAD_ERR_CONTENT, this.downloadError.message, {
            type: mediaType,
            url: this.downloadError.data.request.url,
            status: this.downloadError.data.response.status,
            response: this.getResponseText(this.downloadError.data.response),
            segmentTime: this.downloadError.data.request.startTime,
            segmentDuration: this.downloadError.data.request.duration,
        });

        // Reset download error
        this.downloadError = null;
    }

    private onError (e) {
        // Ignore deprecated errors
        if (e.event) {
            return;
        }
        
        let error = e.error;
        switch (error.code) {
            // HTMLMediaElement
            case 1:
                this.sendErrorEvent(PlayerErrorCode.MEDIA_ERR_ABORTED, error.message);
                break;
            case 2:
                this.sendErrorEvent(PlayerErrorCode.MEDIA_ERR_NETWORK, error.message);
                break;
            case 3:
                this.sendErrorEvent(PlayerErrorCode.MEDIA_ERR_DECODE, error.message);
                break;
            case 4:
                this.sendErrorEvent(PlayerErrorCode.MEDIA_ERR_SRC_NOT_SUPPORTED, error.message);
                break;
            case 5:
                this.sendErrorEvent(PlayerErrorCode.MEDIA_ERR_ENCRYPTED, error.message);
                break;

            // MSE
            case dashjs.MediaPlayer.errors.CAPABILITY_MEDIASOURCE_ERROR_CODE:
                this.sendErrorEvent(PlayerErrorCode.CAPABILITY_ERR_MEDIASOURCE);
                break;

            case dashjs.MediaPlayer.errors.MEDIASOURCE_TYPE_UNSUPPORTED_CODE:
                this.sendErrorEvent(PlayerErrorCode.CAPABILITY_ERR_MEDIASOURCE, error.message);
                break;

            // Download errors
            case dashjs.MediaPlayer.errors.DOWNLOAD_ERROR_ID_MANIFEST_CODE:
                this.sendErrorEvent(PlayerErrorCode.DOWNLOAD_ERR_MANIFEST, error.message, {
                    url: error.data.request.url,
                    status: error.data.response.status,
                    response: this.getResponseText(error.data.response)
                });
                break;
            case dashjs.MediaPlayer.errors.DOWNLOAD_ERROR_ID_INITIALIZATION_CODE:
            case dashjs.MediaPlayer.errors.DOWNLOAD_ERROR_ID_CONTENT_CODE:
                this.downloadError = error;
                this.processDownloadError();
                break;

            // EME
            case dashjs.MediaPlayer.errors.CAPABILITY_MEDIAKEYS_ERROR_CODE:
                this.sendErrorEvent(PlayerErrorCode.CAPABILITY_ERR_MEDIAKEYS, error.message);
                break;

            case dashjs.MediaPlayer.errors.MEDIA_KEYERR_UNKNOWN_CODE:
                this.sendErrorEvent(PlayerErrorCode.MEDIA_KEYERR_UNKNOWN, error.message);
                break;

            case dashjs.MediaPlayer.errors.MEDIA_KEYERR_CLIENT_CODE:
                this.sendErrorEvent(PlayerErrorCode.MEDIA_KEYERR_CLIENT, error.message);
                break;

            case dashjs.MediaPlayer.errors.MEDIA_KEYERR_SERVICE_CODE:
                this.sendErrorEvent(PlayerErrorCode.MEDIA_KEYERR_SERVICE, error.message);
                break;

            case dashjs.MediaPlayer.errors.MEDIA_KEYERR_OUTPUT_CODE:
                this.sendErrorEvent(PlayerErrorCode.MEDIA_KEYERR_OUTPUT, error.message);
                break;

            case dashjs.MediaPlayer.errors.MEDIA_KEYERR_DOMAIN_CODE:
                this.sendErrorEvent(PlayerErrorCode.MEDIA_KEYERR_DOMAIN, error.message);
                break;

            case dashjs.MediaPlayer.errors.KEY_SYSTEM_ACCESS_DENIED_ERROR_CODE:
                this.sendErrorEvent(PlayerErrorCode.MEDIAKEYS_ERR_KEYSYSTEM_ACCESS, error.messageS);
                break;

            case dashjs.MediaPlayer.errors.SERVER_CERTIFICATE_UPDATED_ERROR_CODE:
                this.sendErrorEvent(PlayerErrorCode.MEDIAKEYS_ERR_SERVER_CERTIFICATE, error.message);
                break;

            case dashjs.MediaPlayer.errors.KEY_SESSION_CREATED_ERROR_CODE:
                this.sendErrorEvent(PlayerErrorCode.MEDIAKEYS_ERR_CREATE_SESSION, error.message);
                break;

            case dashjs.MediaPlayer.errors.MEDIA_KEY_MESSAGE_NO_LICENSE_SERVER_URL_ERROR_CODE:
                this.sendErrorEvent(PlayerErrorCode.MEDIAKEYS_ERR_UNKNOWN_LICENSER, error.message);
                break;

            case dashjs.MediaPlayer.errors.MEDIA_KEY_MESSAGE_LICENSER_ERROR_CODE:
                this.sendErrorEvent(PlayerErrorCode.MEDIAKEYS_ERR_LICENSE_REQUEST, error.message, {
                    url: '',
                    code: 0,
                    response: error.message
                });
                break;

            case dashjs.MediaPlayer.errors.MEDIA_KEYERR_CODE:
                this.sendErrorEvent(PlayerErrorCode.MEDIAKEYS_ERR_KEY_ERROR, error.message);
                break;

            case dashjs.MediaPlayer.errors.KEY_STATUS_CHANGED_EXPIRED_ERROR_CODE:
                this.sendErrorEvent(PlayerErrorCode.MEDIAKEYS_ERR_EXPIRED, error.message);
                break;

            default:
                this.sendErrorEvent(PlayerErrorCode.PLAYER_INTERNAL_ERROR, error.message, error);
                break;
        }
    }

/*    
    MANIFEST_LOADER_PARSING_FAILURE_ERROR_CODE:          10;
    MANIFEST_LOADER_LOADING_FAILURE_ERROR_CODE:          12;
    XLINK_LOADER_LOADING_FAILURE_ERROR_CODE:             13;
    SEGMENTS_UPDATE_FAILED_ERROR_CODE:                   14;
    SEGMENTS_UNAVAILABLE_ERROR_CODE:                     15;
    SEGMENT_BASE_LOADER_ERROR_CODE:                      16;
    TIME_SYNC_FAILED_ERROR_CODE:                         17;
    FRAGMENT_LOADER_LOADING_FAILURE_ERROR_CODE:          18;
    FRAGMENT_LOADER_NULL_REQUEST_ERROR_CODE:             19;
    URL_RESOLUTION_FAILED_GENERIC_ERROR_CODE:            20;
    APPEND_ERROR_CODE:                                   21;
    REMOVE_ERROR_CODE:                                   22;
    DATA_UPDATE_FAILED_ERROR_CODE:                       23;


    DOWNLOAD_ERROR_ID_XLINK_CODE:                        30;
    MANIFEST_ERROR_ID_PARSE_CODE:                        32;
    MANIFEST_ERROR_ID_NOSTREAMS_CODE:                    33;
    TIMED_TEXT_ERROR_ID_PARSE_CODE:                      34;
    MANIFEST_ERROR_ID_MULTIPLEXED_CODE:                  35;


    MEDIA_KEY_MESSAGE_NO_CHALLENGE_ERROR_CODE:          108;
    MSS_NO_TFRF_CODE:                                   200;      
*/

    // #endregion PRIVATE FUNCTIONS

}
