import { PlayerEvents, ProtectionData, KeySystemConfiguration, ChallengeFormatType } from '../Types'

const base64 = require('base64-js/base64js.min.js');

/**
 * Dash.js player
 */
export class FairPlayController {
    // #region MEMBERS
    // --------------------------------------------------

    // <video> / HTMLMediaElement
    protected videoElement: HTMLMediaElement;

    // FairPlay key system configuration
    private keySystemConfiguration: KeySystemConfiguration;

    // MediaKeySession (as WebKitMediaKeySession on Safari)
    private mediaKeySession: any;

    // The license request
    private licenseRequest: XMLHttpRequest;

    // Logs state
    private logsEnabled: boolean;

    // Events listeners
    private needKeyListener;
    private keyMessageListener;
    private keyAddedListener;
    private keyErrorListener;

    // #endregion MEMBERS
    // --------------------------------------------------

    // #region CONSTRUCTOR
    // --------------------------------------------------

    /**
     * Constructor.
     */
    constructor () {
    }
    // #endregion CONSTRUCTOR

    // #region PUBLIC FUNCTIONS
    // --------------------------------------------------

    // #region SETUP
    // --------------------------------------------------

    public init (videoElement: HTMLMediaElement, protectionData: ProtectionData) {
        this.videoElement = videoElement;
        if (protectionData && protectionData['com.apple.fps.1_0']) {
            this.keySystemConfiguration = protectionData['com.apple.fps.1_0'];
        }
        this.logsEnabled = true;
        this.mediaKeySession = null;
        this.licenseRequest = null;

        this.needKeyListener = this.onNeedKey.bind(this);
        this.keyMessageListener = this.onKeyMessage.bind(this);
        this.keyAddedListener = this.onKeyAdded.bind(this);
        this.keyErrorListener = this.onKeyError.bind(this);

        this.videoElement.addEventListener('webkitneedkey', this.needKeyListener);
    }

    public reset () {
        if (this.licenseRequest !==  null) {
            this.licenseRequest.abort();
            this.licenseRequest = null;
        }
        this.mediaKeySession = null;
        this.videoElement.removeEventListener('webkitneedkey', this.needKeyListener);
    }

    public enableLogs (enable: boolean) {
        this.logsEnabled = enable;
    }

    // #endregion SETUP
    // --------------------------------------------------

    // #region PRIVATE FUNCTIONS
    // --------------------------------------------------

    private debug (message: string) {
        if (!this.logsEnabled) {
            return;
        }
        console.log('[HLS-FP] ' + message);
    }

    private sendErrorEvent (e) {
        let event = document.createEvent("CustomEvent");
        event.initCustomEvent(PlayerEvents.PLAYER_ERROR, false, false, e);
        this.videoElement.dispatchEvent(event);
    }

    private stringToArray (str: string) {
        let buffer = new ArrayBuffer(str.length * 2); // 2 bytes per character
        let array = new Uint16Array(buffer);
        for (let i = 0; i < str.length; i++) {
            array[i] = str.charCodeAt(i);
        }
        return array;
    }

    private getChallengeFormatType () {
        return (this.keySystemConfiguration && this.keySystemConfiguration.challengeFormat) ?  this.keySystemConfiguration.challengeFormat : ChallengeFormatType.OCTET_STREAM;
    }

    private extractContentId  (initData: Uint8Array) {
        let contentId = String.fromCharCode.apply(null, new Uint16Array(initData.buffer));
        let parts = contentId.split("//");
        if (parts.length != 2) {
          throw "Invalid content key format";
        }
        return parts[1];
    }

    private getCertificate () {
        if (!this.keySystemConfiguration || !this.keySystemConfiguration.serverCertificate) {
            return new Uint8Array(0);
        }
        return base64.toByteArray(this.keySystemConfiguration.serverCertificate);
    }

    private concatInitDataIdAndCertificate (initData: Uint8Array, id: string, cert: Uint8Array) {
        let idArray = (typeof id == "string") ? this.stringToArray(id) : id;

        // layout is [initData][4 byte: idLength][idLength byte: id][4 byte:certLength][certLength byte: cert]
        let offset = 0;
        let buffer = new ArrayBuffer(initData.byteLength + 4 + idArray.byteLength + 4 + cert.byteLength);
        let dataView = new DataView(buffer);

        let initDataArray = new Uint8Array(buffer, offset, initData.byteLength);
        initDataArray.set(initData);
        offset += initDataArray.byteLength;

        dataView.setUint32(offset, idArray.byteLength, true);
        offset += 4;

        let id16Array = new Uint16Array(buffer, offset, id.length);
        id16Array.set(idArray);
        offset += id16Array.byteLength;

        dataView.setUint32(offset, cert.byteLength, true);
        offset += 4;

        let certArray = new Uint8Array(buffer, offset, cert.byteLength);
        certArray.set(cert);

        return new Uint8Array(buffer, 0, buffer.byteLength);
    }

    private processLicenseMessage (message: Uint8Array) {
        if (this.getChallengeFormatType() === ChallengeFormatType.OCTET_STREAM) {
            return message;
        }

        let textMessage = 'spc=' + base64.fromByteArray(message) + '&assetId=' + encodeURIComponent(this.mediaKeySession.contentId);
        return textMessage;
    }

    private sendLicenseRequest (url: string, challenge: any) : Promise<XMLHttpRequest> {
        return new Promise<XMLHttpRequest>((resolve, reject) => {
            let challengeFormatType = this.getChallengeFormatType();
            let licenseRequest = new XMLHttpRequest();
            let success = false;
            licenseRequest.responseType = challengeFormatType === ChallengeFormatType.OCTET_STREAM ? 'arraybuffer' : 'text';

            licenseRequest.onload = function (this: XMLHttpRequest, e: ProgressEvent) {
                if (this.status < 200 || this.status > 299) {
                    return;
                }    
                if (this.status === 200 && this.readyState === 4) {
                    success = true;
                    resolve(this);
                }
            };

            licenseRequest.onloadend = function (this: XMLHttpRequest, e: ProgressEvent) {
                if (success) {
                    return;
                }
                reject(this);
            };

            // licenseRequest.onerror = function (this: XMLHttpRequest, e: ErrorEvent) {
            //     if (success) {
            //         return;
            //     }
            //     reject(this);
            // };            

            licenseRequest.open('POST', url, true);
            licenseRequest.setRequestHeader('Content-Type', challengeFormatType === ChallengeFormatType.OCTET_STREAM ? 'application/octet-stream' : 'application/x-www-form-urlencoded');
            licenseRequest.send(challenge);            
        });
    }

    private processLicenseResponse (request) {
        let challengeFormatType = this.getChallengeFormatType();
        let key;

        if (challengeFormatType === ChallengeFormatType.TEXT) {
            // Response can be of the form: '\n<ckc>base64encoded</ckc>\n', so trim the excess:
            key = request.responseText.trim();
            if (key.substr(0, 5) === '<ckc>' && key.substr(-6) === '</ckc>') {
                key = key.slice(5,-6);
            }
            key = base64.toByteArray(key);
        } else {
            key = new Uint8Array(request.response);
        }

        this.debug('update key session');
        this.mediaKeySession.update(key);
    }

    private getKeyError (event) {
        var error = event.target.error,
            code = 'MEDIA_KEYERR',
            message = "MediakeyError";

        if (error) {
            switch (error.code) {
                case 1:
                    code = 'MEDIA_KEYERR_UNKNOWN';
                    message = "An unspecified error occurred. This value is used for errors that don't match any of the other codes.";
                    break;
                case 2:
                    code = 'MEDIA_KEYERR_CLIENT';
                    message = "The Key System could not be installed or updated.";
                    break;
                case 3:
                    code = 'MEDIA_KEYERR_SERVICE';
                    message = "The message passed into update indicated an error from the license service.";
                    break;
                case 4:
                    code = 'MEDIA_KEYERR_OUTPUT';
                    message = "There is no available output device with the required characteristics for the content protection system.";
                    break;
                case 5:
                    code = 'MEDIA_KEYERR_HARDWARECHANGE';
                    message += "A hardware configuration change caused a content protection error.";
                    break;
                case 6:
                    code = 'MEDIA_KEYERR_DOMAIN';
                    message = "An error occurred in a multi-device domain licensing configuration. The most common error is a failure to join the domain.";
                    break;
                default:
                    code = 'MEDIA_KEYERR_UNKNOWN';
                    message = "An unspecified error occurred. This value is used for errors that don't match any of the other codes.";
                    break;
            }
        } else {
            code = 'MEDIA_KEYERR_UNKNOWN';
            message = "An unspecified error occurred. This value is used for errors that don't match any of the other codes.";
        }
        if (error.systemCode) {
            message += "  (System Code = " + event.systemCode + ")";
        }
        return {
            code: code,
            message: message
        }
    }

    private onNeedKey (e) {
        this.debug("needkey event");

        let contentId = this.extractContentId(e.initData);
        let certificate = this.getCertificate();
        let initData = this.concatInitDataIdAndCertificate(e.initData, contentId, certificate);

        this.debug("create key session");
        let mediaKeys = new window['WebKitMediaKeys']('com.apple.fps.1_0');
        this.videoElement['webkitSetMediaKeys'](mediaKeys);
        this.mediaKeySession = this.videoElement['webkitKeys'].createSession('video/mp4', initData);

        if (!this.mediaKeySession) {
            throw "Could not create key session";
        }

        if (this.mediaKeySession.error) {
            this.sendErrorEvent({
                code: 'MEDIA_KEYMESSERR_NO_SESSION',
                message: 'Failed to create key session',
                data: this.mediaKeySession.error
            });
        }

        this.mediaKeySession.contentId = contentId;

        this.mediaKeySession.addEventListener("webkitkeymessage", this.keyMessageListener);
        this.mediaKeySession.addEventListener("webkitkeyadded", this.keyAddedListener);
        this.mediaKeySession.addEventListener("webkitkeyerror", this.keyErrorListener);
    }

    private onKeyMessage (e) {

        this.debug('keymessage event');

        let serverUrl = this.keySystemConfiguration ? this.keySystemConfiguration.serverURL : null;
        if (!serverUrl) {
            this.sendErrorEvent({
                code:'MEDIA_KEYMESSERR_URL_LICENSER_UNKNOWN',
                message: 'No license server URL specified'
            })
            return;
        }

        let message = e.message;
        message = this.processLicenseMessage(message);
        this.debug("send license request");
        this.sendLicenseRequest(serverUrl, message)
        .then((request) => {
            this.debug("received license reponse");
            this.processLicenseResponse(request);
        })
        .catch((request) => {
            this.sendErrorEvent({
                code: 'MEDIA_KEYMESSERR_LICENSER_ERROR',
                message: 'License request failed',
                data: {
                    status: request.status,
                    error: request.response
                }
            });
        })
    }

    private onKeyAdded (e) {
        this.debug('keyadded event');
    }

    private onKeyError (e) {
        this.debug('keyerror event');
        let error = this.getKeyError(e);
        this.sendErrorEvent(error);
    }

    // #endregion PRIVATE FUNCTIONS

}
